/* start-style:: Tailwind Normalization */
@tailwind base;
html,
body {
	min-height: 100% !important;
}
/* end-style:: EndTailwind Normalization */

/* start-style:: Tailwind Custome Component Classes */
@tailwind components;
/* end-style:: Tailwind Custome Component Classes */

/* start-style:: Tailwind Custome Normalization Classes */
ul,
ol {
	@apply m-0 p-0 list-none;
}

img {
	@apply w-full;
}

p {
	@apply m-0;
}
/* end-style:: Tailwind Custome Normalization Classes */

/* start-style:: Navbar  Component Classes */
.active {
	background-color: rgba(0, 0, 0, 0.15);
	/* color: #1565c0; */
	/* background-color: rgba(0, 0, 0, 0.04); */
}
/* end-style:: Navbar  Component Classes */

/* start-style:: BG  Component Classes */
.bg {
	@apply bg-no-repeat bg-cover;
}

.bg-body {
	background-image: url(./images/bg/bg-body.webp);
}

.bg-light {
	background-color: #f3f6f9;
}

.bg-light-trans {
	background-color: rgba(243, 246, 249, 0.4);
}
/* start-style:: BG  Component Classes */

/* start-style:: Home Hero Section Component Classes */
.bg-home-hero {
	background-image: url(./images/bg/home-hero-bg.svg);
}

#root .home-hero-sec {
	padding: 1.6rem 0;
}
/* end-style:: Home Hero Section Component Classes */

