/* start-style:: Portfolio MUITabs  Component  Customization Classes */
.MuiTabs-scrollButtons.Mui-disabled {
	opacity: 0.3;
}
/* end-style:: Portfolio MUITabs  Component  Customization Classes */

/* start-style:: App Component  Customization Classes */
.App {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	min-height: 100%;
	/* min-height: 100vh; */
	display: grid;
	grid-template-rows: auto 1fr auto;
}
/* end-style:: App Component  Customization Classes */

/* start-style:: MuiBreadcrumb Component  Customization Classes */
.MuiBreadcrumbs-ol {
	justify-content: center;
}
/* end-style:: MuiBreadcrumb Component  Customization Classes */

/* start-style:: Project Card Component  Customization Classes */
/* https://www.arshakir.com/work */
.MuiCard-root.project-card__large .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);
	display: block;
}

.MuiCard-root.project-card__large:hover .MuiCardMedia-root {
	transform: translate3d(0px, -85%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 7s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.MuiCard-root.project-card__small .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 0.5s cubic-bezier(0.17, 0.67, 0.83, 0.67);
	width: 100%;
	height: 100%;
	display: block;
	object-fit: cover;
}

.MuiCard-root.project-card__small:hover .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1.2, 1.2, 1.2) rotateX(0deg)
		rotateY(0deg) rotateZ(5deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}
/* end-style:: Project Card Component  Customization Classes */

/* start-style:: Tailwind utilities Classes */
@tailwind utilities;
/* end-style:: Tailwind utilities Classes */

